
import { defineComponent, ref, reactive, computed, onMounted } from 'vue'
import { ElMessage, ElMessageBox } from 'element-plus'
import { useRouter } from 'vue-router'
import { useI18n } from 'vue-i18n'
import { ElForm, ElUpload } from 'element-plus'
import SubTable from '@/components/sub-table'
import { FormModel } from '../detail/type/index'
import { PageInfo } from '../uploadpicture/types/index'

import {
  customerStyleList,
  activeFlag,
  creditList,
  isDefaultList,
  countrytList,
  cityList,
  provinceList
} from '@/constant/option'
export default defineComponent({
  name: 'uploadpicture',
  components: {
    SubTable,
    ElUpload
  },
  setup() {
    const { t } = useI18n()
    const data = ref<any>([])
    const fileList = ref([])
    const router = useRouter()
    const handleRemove = (file: any, uploadFiles: any) => {
      console.log(file, uploadFiles)
    }
    const handlePreview = (uploadFile: any) => {
      console.log(uploadFile)
    }
    const handleSuccess = (file: any) => {
      console.log(file, 'çˆ¶filesuccess')
    }
    const beforeUpload = (file: any) => {
      console.log(file)
      if (file.type !== 'image/jpeg') {
        ElMessage.error('Avatar picture must be JPG format!')
        return false
      } else if (file.size / 1024 / 1024 > 2) {
        ElMessage.error('Avatar picture size can not exceed 2MB!')
        return false
      }
      return true
    }
    const beforeRemove = (uploadFile: any, uploadFiles: any) => {
      console.log(uploadFile, uploadFiles)
    }
    const isDefaultArray = computed(() => isDefaultList)
    const countrytArray = computed(() => countrytList)
    const cityArray = computed(() => cityList)
    const provinceArray = computed(() => provinceList)
    const dialogVisible = ref(false)
    const searchInfo = () => {
      const params = Object.assign(
        {},
        {
          pageSize: pageInfo.value.pageSize,
          currentPage: pageInfo.value.currentPage
        }
      )
      console.log(params, 'params')
    }
    const handleSizeChange = (val: number) => {
      pageInfo.value.pageSize = val
      searchInfo()
    }
    const handleCurrentChange = (val: number) => {
      pageInfo.value.currentPage = val
      searchInfo()
    }
    const pageInfo = ref<PageInfo>({
      totalCount: 100,
      pageSizes: [10, 20, 50, 100],
      handleSizeChange: handleSizeChange,
      handleCurrentChange: handleCurrentChange,
      currentPage: 1,
      pageSize: 20
    })
    const handleRow = (rowData: any, label: any) => {
      //   addressFormRef.value?.resetFields()
      if (label === 'Edit') {
        dialogVisible.value = true
      }
      if (label === 'Delete') {
        ElMessageBox.confirm(t('deleteChange'), t('deleteTips'), {
          distinguishCancelAndClose: true,
          confirmButtonText: t('confirm'),
          cancelButtonText: t('cancel'),
          customClass: 'message-box',
          center: true
        })
          .then(() => {
            ElMessage({
              type: 'success',
              message: t('deleteSuccessful')
            })
          })
          .catch((action: any) => {
            ElMessage({
              type: 'info',
              message: action === 'cancel' ? t('deleteFailed') : t('canceled')
            })
          })
      }
    }
    const subTableConfig = ref({
      showSelectColumn: false,
      showIndexColumn: false,
      border: false,
      align: 'left',
      light: true,
      propList: [
        {
          prop: 'productImage',
          label: t('productImage'),
          minWidth: '100',
          type: 'image'
        }
      ],
      isOperation: true,
      operation: {
        label: t('operation'),
        // width: '250',
        cols: [
          {
            label: t('edit'),
            type: 'primary',
            handleRow: handleRow,
            icon: 'el-icon-download'
          },
          {
            label: t('delete'),
            type: 'danger',
            handleRow: handleRow,
            icon: 'el-icon-delete'
          }
        ]
      }
    })
    let formModel = ref<FormModel>({ activeFlag: 1 })
    onMounted(() => {
      data.value = [
        {
          productImage:
            'https://img2.baidu.com/it/u=1885222319,1935280267&fm=253&app=138&size=w931&n=0&f=JPEG&fmt=auto?sec=1680022800&t=ce3dadfe9a92fc6414e18b97c971c80c'
        }
      ]
      //   console.log(
      //     router.currentRoute.value.query,
      //     'router.currentRoute.value.query'
      //   )
    })

    const customerStyleArray = computed(() => customerStyleList)
    const activeFlagArray = computed(() => activeFlag)
    const creditArray = computed(() => creditList)
    const formRef = ref<InstanceType<typeof ElForm>>()
    const rules = reactive({
      barcode: [{ required: true, trigger: 'change' }],
      categoryCode: [{ required: true, trigger: 'change' }]
    })
    const cancel = () => {
      formRef.value?.resetFields()
      router.push({ path: '/main/Inventory/commodityEntry/newCommodity' })
    }
    const submitForm = () => {
      router.push({ path: '/main/Inventory/commodityEntry/warehouse' })
    }
    return {
      formModel,
      rules,
      cancel,
      submitForm,
      formRef,
      customerStyleArray,
      activeFlagArray,
      creditArray,
      t,
      data,
      isDefaultArray,
      dialogVisible,
      countrytArray,
      cityArray,
      provinceArray,
      ElMessage,
      pageInfo,
      subTableConfig,
      fileList,
      handleRemove,
      handlePreview,
      beforeRemove,
      beforeUpload,
      handleSuccess
    }
  }
})
