
import { defineComponent, computed } from 'vue'
import { formatDate } from '@/utils/format-date'
export default defineComponent({
  props: {
    showSelectColumn: {
      type: Boolean,
      default: false
    },
    showIndexColumn: {
      type: Boolean,
      default: false
    },
    border: {
      type: Boolean,
      default: false
    },
    data: {
      type: Array,
      required: true
    },
    propList: {
      type: Array,
      required: true
    },
    isOperation: {
      type: Boolean,
      default: false
    },
    operation: {
      type: Object,
      required: true
    },
    pageInfo: {
      type: Object,
      required: true
    },
    align: {
      type: String,
      default: 'left'
    },
    light: {
      type: Boolean,
      default: false
    }
  },
  components: {},
  emits: ['selectionChange'],
  setup(props, { emit }) {
    const currentPage = computed(() => {
      return props.pageInfo.currentPage
    })
    const pageSize = computed(() => {
      return props.pageInfo.pageSize
    })
    const handleSelectionChange = (value: any) => {
      emit('selectionChange', value)
    }

    const isShow = computed(() => {
      return props.isOperation && props.operation.cols.length
    })
    const filters = (row: any, options: any) => {
      let list = options.filter((e: any) => e.value === row)
      return list.length > 0 ? list[0].label : '--'
    }
    const timeFilters = (row: any) => {
      if (row) {
        return formatDate(row)
      }
      return '--'
    }

    return {
      handleSelectionChange,
      isShow,
      filters,
      timeFilters,
      currentPage,
      pageSize
    }
  }
})
